import * as React from "react";
import { DataTable } from "primereact/datatable";
import { Column } from "primereact/column";
import { Card } from "primereact/card";
import picks from "../../data/picks.json";

/**
 * This component renders the data table
 */
function PicksTable() {
  // Reformat the data
  // ["id": "KC", "Bob": 12, "Laurie": ]
  const teams = Object.keys(picks.data.Bob);
  const formattedData = teams.map((team) => {
    const users = Object.keys(picks.data);

    let userPick = {};
    users.forEach(function (user) {
      userPick[user] = picks.data[user][team];
    });
    return {
      ...userPick,
      id: team
    };
  });

  return (
    <div>
      <Card title="Picks">
        <DataTable
          value={formattedData}
          size="small"
          responsiveLayout="scroll"
          stripedRows
          showGridlines
        >
          <Column field="id" header=""></Column>
          <Column field="Adam" header="Adam" sortable></Column>
          <Column field="Amy" header="Amy" sortable></Column>
          <Column field="Bob" header="Bob" sortable></Column>
          <Column field="Brian" header="Brian" sortable></Column>
          <Column field="Dave" header="Dave" sortable></Column>
          <Column field="Frank" header="Frank" sortable></Column>
          <Column field="Jim" header="Jim" sortable></Column>
          <Column field="Jo" header="Jo" sortable></Column>
          <Column field="Laurie" header="Laurie" sortable></Column>
          <Column field="Matt" header="Matt" sortable></Column>
          <Column field="Monica" header="Monica" sortable></Column>
          <Column field="Tony" header="Tony" sortable></Column>
        </DataTable>
      </Card>
    </div>
  );
}

export default PicksTable;
